@import url("https://fonts.googleapis.com/css?family=Roboto+Mono");
html {
  background-color: #222;
  font-family: "Roboto Mono", monospace;
  /* font-size: 0.8em; */
  color: #f7edee;
}
#container {
  display: flex;
  flex-wrap: wrap;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
svg {
  background-color: #42171e;
}
.draggable {
  fill: rgba(145, 23, 41, 0.8);
  cursor: grab;
}
.draggable text {
  fill: rgba(247, 237, 238, 0.6);
}
.draggable:hover,
.dragging {
  fill: rgba(186, 31, 54, 0.8);
  cursor: grabbing;
}
.draggable:hover text,
.dragging text {
  fill: rgba(255, 255, 255, 0.8);
}
text {
  font-size: 0.8em;
}
.draggable text,
.dragging text {
  user-select: none;
}
path#curve {
  stroke: #f7edee;
  stroke-width: 20px;
  fill: transparent;
}
line.helper {
  stroke: rgba(145, 23, 41, 0.8);
  stroke-width: 2px;
  fill: transparent;
}
.code {
  margin: 50px;
}
pre {
  font-family: "Roboto Mono", monospace;
}
pre p {
  margin-top: 10px;
  margin-bottom: 10px;
}
pre .ident1 {
  margin-left: 10px;
}
pre .ident2 {
  margin-left: 30px;
}
